import React from 'react';
import { NavLink, Link } from 'react-router-dom';
import { HiShoppingBag } from 'react-icons/hi';
import { useAuth } from '../../context/auth';
import toast from 'react-hot-toast';
import SearchInput from '../Form/SearchInput';
import useCategory from './../../hooks/useCategory';
import { useCart } from '../../context/cart';
import { Badge } from 'antd';

const Header = () => {
    const {auth , setAuth} = useAuth();
    const { cart } = useCart();
    const categories = useCategory();
    const handleLogout = () => {
        setAuth({
            ...auth, user: null , token: "",
        })
        localStorage.removeItem("auth");
        toast.success('Logout');
    }
    return (
        <>
            <nav className="navbar navbar-expand-lg bg-body-tertiary">
                <div className="container-fluid">
                    <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarTogglerDemo01" aria-controls="navbarTogglerDemo01" aria-expanded="false" aria-label="Toggle navigation">
                        <span className="navbar-toggler-icon" />
                    </button>
                    <div className="collapse navbar-collapse" id="navbarTogglerDemo01">
                    <NavLink to="/" className="navbar-brand" href="#"><HiShoppingBag/> eShop </NavLink>
                    <ul className="navbar-nav ms-auto mb-2 mb-lg-0">
                        <SearchInput />
                        <li className="nav-item">
                            <NavLink to="/" className="nav-link active">
                                Home
                            </NavLink>
                        </li>
                        <li className="nav-item dropdown">
                            <Link className="nav-link dropdown-toggle" to='/categories' data-bs-toggle="dropdown" aria-expanded="false">
                                Categories
                            </Link>
                            <ul className="dropdown-menu">
                                <li>
                                    <Link to="/categories">
                                        All Category
                                    </Link>
                                </li>
                                { categories?.map((c) => (
                                    <li key={c._id}>
                                        <Link className="dropdown-item" to={`/category/${c.slug}`}> 
                                            {c?.name} 
                                        </Link>
                                    </li>
                                ))}
                            </ul>
                        </li>
                        { !auth.user ? ( <>
                                <li className="nav-item">
                                    <NavLink to="/register" className="nav-link"> 
                                        Register 
                                    </NavLink>
                                </li>
                                <li className="nav-item">
                                    <NavLink to="/login" className="nav-link"> 
                                        Login 
                                    </NavLink>
                                </li> 
                            </> ) : ( <>
                                <li className="nav-item dropdown">
                                    <NavLink className="nav-link dropdown-toggle" role="button" data-bs-toggle="dropdown">
                                        { auth?.user.name }
                                    </NavLink>
                                    <ul className="dropdown-menu">
                                        <li><NavLink  to={`/dashboard/${auth?.user?.role !== 1 ? 'user': 'admin'}`} className="dropdown-item"> Dashboard </NavLink></li>
                                        <li className="dropdown-item">
                                            <NavLink onClick={handleLogout} to="/login" className="nav-link"> 
                                                Logout 
                                            </NavLink>
                                            </li>
                                    </ul>
                                </li>
                            </> )        
                        }    
                        <li className="nav-item">
                            <Badge count={cart?.length} showZero>
                                <NavLink to="/cart" className="nav-link"> 
                                    Cart 
                                </NavLink>
                            </Badge>
                        </li>
                    </ul>
                    </div>
                </div>
            </nav>

        </>
    )
}

export default Header
