import React from 'react';
import Layout from './../components/Layout/layout';
import { useCart } from '../context/cart';
import { useAuth } from '../context/auth';
import {useNavigate} from 'react-router-dom';
import axios from 'axios';
import toast from 'react-hot-toast';

const CartPage = () => {
    const { auth , setAuth} = useAuth();
    const { cart , setCart} = useCart();
    const navigate = useNavigate();
    //price function
    const totalPrice = () => {
        try{
            let total = 0;
            cart?.map(item => { total +=  item.price});
            return total.toLocaleString("en-us", {
                style : "currency",
                currency : "USD",
            });
        }
        catch(error){
            toast.error("Something went Wrong");
        }
    }
    const removeCartItem = (pid) => {
        try{
            let myCart = [...cart];
            let index = myCart.findIndex(item => item._id === pid);
            myCart.splice(index,1);
            setCart(myCart);
            localStorage.setItem("cart" , JSON.stringify(myCart));
        }
        catch(error){
            toast.error("Something went Wrong");
        }
    }

    const handlePayment = async() => {
        try{
            const { data } = await axios.post(`${process.env.REACT_APP_API}/api/v1/auth/place-order`,{cart , auth});
            if(data?.success){
                localStorage.removeItem("cart");
                setCart([]);
                navigate("/dashboard/user/orders");
                toast.success("Cart Ordered Successfully");
            }
            else{
                toast.error("Something went wrong");
            }
        }
        catch(error){
            toast.error("Something went Wrong");
        }
    }

    return (
        <Layout>
            <div className='container'>
                <div className='row'>
                    <div className="col-md-12">
                        <h1 className="text-center bg-light p-2 mb-1" >
                            { `Hello ${auth?.token && auth?.user?.name}`}
                        </h1>
                        <h4 className='text-center'>
                            { cart?.length ? 
                                `You have ${cart.length} items in your cart ${auth?.token ? "" : "Please login to checkout" }`
                                : "Your cart is Empty"
                            }
                        </h4>
                    </div>
                </div>
                <div className="row">
                    <div className="col-md-9">
                            {
                                cart?.map( p => (
                                    <div className="row m-2 border border-2 p-2" key={p._id} >
                                        <div className="col-md-4">
                                            <img 
                                                src={`/api/v1/product/product-photo/${p._id}`} 
                                                className="card-img-top" 
                                                alt={p.name} width="100px" height="150px">
                                            </img>        
                                        </div>
                                        <div className='col-md-8'>
                                            <h4> {p.name} </h4>
                                            <p> {p.description} </p>
                                            <p> {p.price} </p>
                                            <button 
                                                className='btn btn-danger'
                                                onClick= {() => removeCartItem(p._id)}> 
                                                    Remove 
                                            </button>
                                        </div>       
                                    </div>
                                ))
                            }
                           
                    </div>
                    <div className='col-md-3'>
                        <h2> Cart Summary </h2>
                        <p> Total | Checkout | Payment </p>
                        <hr/>
                        <h4> Total : { totalPrice() } </h4>
                        <button 
                            className='btn btn-outline-primary mb-3' 
                            onClick={handlePayment}
                        > 
                            Buy Now 
                        </button>
                        { auth?.user?.address ? (
                            <>
                                <div className='mb-3'>
                                    <h4> Current Address </h4>
                                    <h5> {auth?.user?.address} </h5>
                                    <button className="btn btn-outline-warning"
                                    onClick={ () => navigate('/dashboard/user/profile')}> 
                                        Update address 
                                    </button>
                                </div>
                            </>
                        ) : (
                            <div className='mb-3'>
                                { auth?.token ? (
                                    <button className='btn btn-outline-warning' 
                                    onClick={() => navigate('/dashboard/user/profile')}>
                                        Update Address
                                    </button>
                                ) : (
                                    <button className='btn btn-outline-warning' 
                                    onClick={() => navigate('/login' , {
                                        state : "/cart",
                                    })}> 
                                        Please Login to checkout 
                                    </button>
                                )}
                            </div>
                        ) }
                    </div>
                </div>
            </div>
        </Layout>
    )
}

export default CartPage