import React , {useState , useEffect} from 'react'
import Layout from './../components/Layout/layout';
import { useParams } from 'react-router-dom';
import axios from 'axios';
import toast from 'react-hot-toast';

const ProductDetails = () => {
    const params = useParams();
    const [product ,setProduct] = useState([]);
    const [relatedProducts , setRelatedProducts] = useState([]);

    // Initial details
    useEffect(() => {
        if(params?.slug)
            getProduct();
    },[params?.slug])

    const getProduct = async() => {
        try{
            const {data} = await axios.get(`${process.env.REACT_APP_API}/api/v1/product/get-product/${params.slug}`)
            setProduct(data?.product);
            getSimilarProduct(data?.product._id , data?.product.category._id);
        }
        catch(error){
            toast.error("Something went Wrong");
        }
    }
    // Similar function
    const getSimilarProduct = async(pid,cid) => {
        try{
            const {data} = await axios.get(`${process.env.REACT_APP_API}/api/v1/product/related-product/${pid}/${cid}`)
            setRelatedProducts(data?.products);
        }
        catch(error){
            toast.error("Something went Wrong");
        }
    }
    return (
        <Layout>
            <div className='row container'>
                <div className='col-md-6'>
                    <img 
                        src={`/api/v1/product/product-photo/${product._id}`} 
                        className="card-img-top" 
                        alt={product.name} />
                </div>
                <div className='col-md-6 '>
                    <h1> Product Details </h1>
                    <h2> Name : {product?.name} </h2>
                    <h3> Price : {product?.price} </h3>
                    <h3> Description :  {product?.description } </h3>
                    <h3> category : {product?.category?.name} </h3>
                    <button className='btn btn-secondary ms-1'> Add To Cart </button>
                </div>
            </div>
            <h1 className='row m-2'> Similar Products </h1>
            { relatedProducts.length < 1 && <p> No similar Product Found </p>} 
            <div className='d-flex flex-wrap' >
              { relatedProducts?.map(p => (
                <div className="card p-3" key={p._id} style={{width: '18rem'}}>
                    <img src={`/api/v1/product/product-photo/${p._id}`} className="card-img-top" alt={p.name} />
                    <div className="card-body">
                        <h5 className="card-title"> {p.name} </h5>
                        <p className="card-text"> {p.description.substring(0,10)} </p>
                        <div className="card-text"> $ {p.price} </div>
                        <button className="btn btn-secondary ms-1"> Add To Cart </button>
                    </div>
                </div>
                ))}
              </div>
        </Layout>
    )
}

export default ProductDetails